import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Navigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { AuthTabs, Button, Input } from '../../components';
import { authorizeUserAsync } from '../../actions';
import { useResetForm } from '../../hooks';
import { selectUserRole } from '../../selectors';
import { ROLE } from '../../constants';
import styled from 'styled-components';

const authFormSchema = yup.object().shape({
    login: yup
        .string()
        .required('  .')
        .matches(/\w+$/, '  .     .')
        .min(3, '  .    3 .')
        .max(15, '  .    15 .'),
    password: yup
        .string()
        .required('  .')
        .matches(/^[\w#%]+$/, '  .  ,    # %.')
        .min(6, '  .    6 .')
        .max(30, '  .    30 .'),
});

const AuthorizationContainer = ({ className }) => {
    const {
        register,
        reset,
        handleSubmit,
        formState: { errors },
    } = useForm({
        defaultValues: {
            login: '',
            password: '',
        },
        resolver: yupResolver(authFormSchema),
    });

    const [serverError, setServerError] = useState(null);

    const dispatch = useDispatch();

    const roleId = useSelector(selectUserRole);

    useResetForm(reset);

    const onSubmit = ({ login, password }) => {
        dispatch(authorizeUserAsync(login, password))
            .then(() => {
                console.info(' ');
            })
            .catch((error) => {
                setServerError(` : ${error.message}`);
            });
    };

    const formError = errors?.login?.message || errors?.password?.message;
    const errorMessage = formError || serverError;

    if (roleId !== ROLE.GUEST) {
        return <Navigate to="/" />;
    }

    return (
        <div className={className}>
            <div className="container">
                <AuthTabs active={'login'} />

                <form className="auth-form" onSubmit={handleSubmit(onSubmit)}>
                    <Input
                        padding={'0.8rem'}
                        height={'auto'}
                        type="text"
                        placeholder="..."
                        {...register('login', {
                            onChange: () => setServerError(null),
                        })}
                    />
                    <Input
                        padding={'0.8rem'}
                        height={'auto'}
                        type="password"
                        placeholder="..."
                        {...register('password', {
                            onChange: () => setServerError(null),
                        })}
                    />
                    <Button type="submit" padding={'1rem'}>
                        
                    </Button>
                    {errorMessage && <div className="auth-form-error">{errorMessage}</div>}
                </form>
            </div>
        </div>
    );
};

export const Authorization = styled(AuthorizationContainer)`
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: calc(100vh - 242px);

    & .container {
        width: 400px;
        max-width: 400px;
        padding: 2rem;
        background: white;
        border-radius: 10px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    }

    & .auth-form {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;

        & input:focus {
            outline: none;
            border-color: #ff4081;
            box-shadow: 0 0px 3px rgba(255, 64, 129, 1);
        }
    }

    & .auth-form-error {
        text-align: center;
        background-color: #ffd1d1;
        padding: 10px;
        color: red;
    }
`;
